@import '../../variables.scss';

p {
    margin:0;
    padding: 0;
}

.banking {
  .dashboard {
      background-color: $BgBlack;
      .dashboardContainer {
          text-align: center;
          padding-top: 40px;
          padding-bottom: 47px;
          .title {
              font-size: 36px;
              color: $PureWhite;
              font-weight: 700;
          }
          .description {
              margin-top: 14px;
              font-size: 16px;
              font-weight: 400;
              color: $PureWhite;
          }
          .tilesContainer {
              margin-top: 30px;
              color: $PureWhite;
              .tile {
                  padding: 30px;
                  border-radius: 8px;
                  height: 200px;
                  display: flex;
                  flex-direction: column;
                  align-items: center;
                  justify-content: center;
                  .label {
                      font-size: 14px;
                      color: $PureWhite;
                      font-weight: 400;
                  }
                  .value {
                      font-size: 32px;
                      color: $PureWhite;
                      font-weight: 500;
                      margin-top: 4px;
                      margin-bottom: 15px;
                  }
                  .buttonGroup {
                      display: flex;
                      flex-direction: row;
                      .boostBtn {
                          margin-left: 12px;
                          padding: 7px 17px 7px 14px;
                          background: linear-gradient(to right, $Green, $Green2);
                          border: 0;
                          border-radius: 22px;
                          text-decoration: none;
                          &:hover {
                            background: linear-gradient(to right, lighten($Green, 5%), lighten($Green2, 5%));
                          }
                          >img {
                              margin-right: 2px;
                          }
                          >span {
                              font-size: 14px;
                              font-weight: 700;
                              color: $PureWhite;
                          }
                      }
                  }
                  .apy {
                      padding: 0 8px;
                      border-radius: 12px;
                      .apyTitle {
                          font-size: 12px;
                          line-height: 24px;
                          font-weight: 400;
                      }
                      .apyNum {
                          font-size: 14px;
                          line-height: 24px;
                          font-weight: 700;
                          margin-left: 10px;
                          
                      }
                  }
              }
              .supplyTile {
                  background: rgba($Green, 0.58);
                  .apy {
                      background-color: $Green;
                  }
              }
              .borrowTile {
                    background: rgba($Blue, 0.5);;
                  .apy {
                      background-color: $Blue;
                  }
              }
              .netTile {
                    background: rgba($PureWhite, 0.2);
                    .logo {
                        height: 28px;
                        width: 28px;
                        border-radius: 50%;
                    }
                    .label {
                        margin-top: 5px;
                    }
              }
          }
          .loanPercentWrap {
            display:flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin-top: 50px;
            .loanPercentInner {
                position: relative;
                width: 100%;
                display:flex;
                align-items: center;
                flex-direction: column;
                .loanLimitContainer {
                    margin-top: 16px;
                    width: 100%;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    align-items: flex-end;
                    .minLoan,
                    .maxLoan {
                        font-size: 14px;
                        line-height: 24px;
                        word-break: break-all;
                        color: $PureWhite;
                    }
                    .maxLoan {
                        text-align: right;
                    }
                    .usedLoan {
                        display:flex;
                        flex-direction: row;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        color: $PureWhite;
                        font-size: 14px;
                        line-height: 24px;
                    }
                    .usedLoanP {
                        margin-left: 5px;
                    }
                    .usedLoanT {
                        display: flex;
                        font-size: 14px;
                        line-height: 24px;
                        color: $PureWhite;
                        .usedPercent {
                            margin-right: 5px;
                        }
                    }
                }
            }
        }
      }
  }
  .accounts {
      background: linear-gradient(900deg, $BgBlack 62px, $Bg 0%);
      .accountsContainer {
          padding-bottom: 40px;
          :global {
              .row {
                  .col-sm-12 {
                      padding: 12px;
                  }
              }
          }
          .tile {
              height: 100%;
              background-color: $PureWhite;
              border-radius: 8px;
              padding: 20px;
              .title {
                  font-size: 18px;
                  color: $DarkBlack;
                  font-weight: 500;
                  border-bottom: 0.5px solid $LineGrey;
                  padding-bottom: 12px;
              }
              thead {
                  tr {
                      th {
                          padding: 20px 12px;
                          color: $DarkGrey;
                          text-align: right;
                          font-size: 14px;
                          font-weight: 400;
                          vertical-align: middle;
                          white-space: nowrap;
                          border-bottom: 1px solid $LineGrey;
                          &:first-child {
                              text-align: left;
                          }
                      }
                  }
              }
              tbody {
                  tr {
                      background: $PureWhite;
                      td {
                          padding: 10px 10px;
                          vertical-align: middle;
                          border-bottom: 1px solid $LineGrey;
                          .asset {
                              display: flex;
                              flex-direction: row;
                              justify-content: flex-start;
                              align-items: center;
                              .name {
                                  margin-left: 8px;
                                  font-size: 14px;
                                  color: $DarkBlack;
                                  font-weight: 400;
                              }
                          }
                          .dataContainer {
                              display: flex;
                              flex-direction: column;
                              justify-content: flex-end;
                              align-items: flex-end;
                              :global {
                                  .btn-sm {
                                      padding: 8px 16px;
                                      white-space: nowrap;
                                  }
                              }
                              .dataPrimary {
                                  font-size: 16px;
                                  font-weight: 400;
                                  color: $DarkBlack;
                                  text-align: right;
                              }
                              .dataSecondary {
                                  margin-top: 4px;
                                  font-size: 12px;
                                  color: $Grey;
                                  font-weight: 400;
                                  text-align: right;
                              }
                          }
                      }
                  }
              }
              .emptyMessage {
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  padding-top: 72px;
                  padding-bottom: 119px;
                  font-size: 14px;
                  .message {
                      font-size: 16px;
                      color: $DarkenGrey;
                      font-weight: 400;
                      margin-bottom: 8px;
                      margin-top: 14px;
                  }
                  .description {
                      font-size: 14px;
                      color: $Grey;
                      font-weight: 300;
                      text-align: center;
                  }
              }
          }
      }
  }
  .marketsData {
      .marketsDataContainer {
          padding-top: 40px;
          padding-bottom: 80px;
      }
  }
}


@media (max-width: 767.98px) { 
    .banking {
        .dashboard {
            .dashboardContainer {
                .tilesContainer {
                    .savingWrap,
                    .loanWrap {
                        order:1
                    }
                    .colletWrap {
                        order:2;
                        margin-top: 20px;
                    }
                }
            }
        }
    }
}


:global {
    .percent-tooltips {
        p {
            margin: 0;
            margin-top: 5px;
            padding: 0;
            &:first-child {
                margin-top: 0;
            }
        }
    }
}